.product{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
    background: #000;

    &:last-child{
        margin-bottom: 0;
        padding-bottom: 25px;
    }

    &__image{
        display:block;
        background-color: #2A265F;
        min-height: 150px;
        min-width: 300px;
        border-radius: 10px;
        box-sizing: border-box;
        position: relative;
        margin-bottom: 15px;
    }

    &__title{
        color: #fff;
        margin-bottom: 15px;
        text-align: center;

        span{
            color: #fff;
            font-weight: 600;
        }
    }
}